<template>
    <div class="home-msg">
        <div class="home-msg-cont">
            <product-name :item="{}" :code="code"></product-name>
            <template-idis-code :code="code"></template-idis-code>
            <product-info
                :item="{ value: { showTrace: false } }"
                :code="code"
                :code-attrs="nonRefAttrs"
                :batch="batch"
            ></product-info>
            <template-raw-material
                v-if="attrCategoryNameList.length > 0"
                :nameAttr="attrCategoryNameList"
                :idisCode="idisCode"
            >
            </template-raw-material>
            <template-idis-reference v-for="attr in refAttrs" :key="attr.id" :attr="attr"> </template-idis-reference>
        </div>
    </div>
</template>

<script>
const defaultIcon = require('@/assets/images/logo.png')
import { getJsonResult } from '@/utils/server-wrapper'
import ProductInfo from '@/components/product-template/ProductInfo'
import ProductName from '@/components/product-template/ProductName'
import TemplateIdisCode from '@/components/product-template/TemplateIdisCode'
import TemplateIdisReference from '@/components/product-template/TemplateIdisReference'
import TemplateRawMaterial from '@/components/product-template/TemplateRawMaterial'

export default {
    data() {
        return {
            defaultIcon,
            userInfo: {},
            idisCode: '',
            tenantId: 0,
            batch: {},
            code: {},
            nonRefAttrs: [],
            refAttrs: [],
            attrCategoryNameList: []
        }
    },
    components: {
        ProductInfo,
        ProductName,
        TemplateIdisCode,
        TemplateIdisReference,
        TemplateRawMaterial
    },
    methods: {},
    async beforeRouteEnter(to, from, next) {
        const idisCode = to.query.code
        const info = await getJsonResult(`/wx/codeInformation?codeId=${encodeURIComponent(idisCode)}`)
        const code = info.code
        const tenantId = code.tenantId
        const batch = info.batch
        const codeAttr = info.codeAttr

        //进行去重处理
        const categoryNames = new Set()
        const attrCategoryNameList = codeAttr
            .filter(attr => attr.attrCategoryName)
            .sort((a, b) => a.attrCategorySort - b.attrCategorySort)
            .map(attr => {
                let name = attr.attrCategoryName
                return name.substring(0, name.indexOf('/'))
            })
            .filter(catName => {
                if (categoryNames.has(catName)) {
                    return false
                }
                categoryNames.add(catName)
                return true
            })
        // const userInfo = (await getJsonResult('/wx/consumer-info')) || {}

        next(vm => {
            vm.$data.idisCode = idisCode
            vm.$data.code = code
            // vm.$data.userInfo = userInfo
            vm.$data.tenantId = tenantId
            vm.$data.nonRefAttrs = codeAttr.filter(c => c.references.length == 0)
            vm.$data.refAttrs = codeAttr.filter(c => c.references.length > 0 && !c.attrCategoryName)
            vm.$data.batch = batch
            vm.$data.attrCategoryNameList = attrCategoryNameList
        })
    }
}
</script>

<style lang="scss" scoped>
.home-msg {
    flex-shrink: 0;
    background-size: cover;
    &-cont {
        background-color: #f4f5f5;
    }
}
.mc-wrap {
    background-color: #ffffff;
    padding: 15px;
    margin-top: 5px;
    margin-bottom: 5px;
    &-title {
        color: #1c2023;
        font-size: 16px;
        text-align: left;
    }
}
</style>
