<template>
    <!--产品信息-->
    <div class="mc-info">
        <!--头部-->
        <div class="flex-sb mb10">
            <!-- <special-title title="产品信息" enTitle="PRODUCT INFORMATION"></special-title> -->
            <div class="public-title">
                <div>产品信息</div>
                <img src="@/assets/images/productInfo-icon.png" alt="" />
            </div>
            <div class="mc-info-sySearch" v-show="item.value.showTrace && sourceFlag === 0" @click="convertRouter">
                <!-- <a :href="`/wx/trace-source?idisCode=${code.idisCode}`" target="_blank" class="mc-info-btn"></a> -->
            </div>
        </div>
        <div class="mc-info-content">
            <table class="mc-info-cont">
                <tr :class="[{ dash: i === 0 }]" v-for="(io, i) in infoList" :key="i">
                    <td :class="['mc-info-cell', 'mc-info-cell-left']">
                        <div class="c-1C2023" style="word-break:break-all;">{{ io.name }}</div>
                    </td>
                    <td :class="['mc-info-cell', 'mc-info-cell-right']">
                        <p class="c-1C2023" style="word-break:break-all;color: #4970e0;" v-if="io.isUrl">
                            <a :href="io.value">{{ io.value }}</a>
                        </p>
                        <p class="c-1C2023" style="word-break:break-all;color: #4970e0;" v-else>{{ io.value }}</p>
                    </td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
import SpecialTitle from './SpecialTitle'

export default {
    props: {
        item: Object,
        code: Object,
        codeAttrs: Array,
        batch: Object,
        sourceFlag: Number | String,
        isView: Boolean
    },
    mounted() {},
    // watch: {
    //     sourceFlag(value) {
    //         this.item.sourceFlag = value
    //     }
    // },
    computed: {
        infoList() {
            let res = []
            if (!this.isView) {
                res = [
                    { name: '对象名称', value: this.code.productName, isUrl: false },
                    { name: '对象编号', value: this.code.productNo, isUrl: false },
                    { name: '对象条码', value: this.code.barCode, isUrl: false },
                    { name: '对象分类', value: this.code.categoryName, isUrl: false },
                    { name: '对象单位', value: this.code.unit, isUrl: false }
                ].filter(i => i.value && i.value.trim())
            }

            if (this.codeAttrs) {
                for (let i of this.codeAttrs) {
                    // if ((!i.references || i.references.length == 0) && !(i.attrCategoryId || i.attrCategoryName)) {

                    // }
                    if (i.attrValue.startsWith('http') || i.attrValue.startsWith('HTTP')) {
                        res.push({
                            name: i.attrNameCn,
                            value: i.attrValue,
                            isUrl: true
                        })
                    } else {
                        res.push({
                            name: i.attrNameCn,
                            value: i.attrValue,
                            isUrl: false
                        })
                    }
                }
            }
            return res
        }
    },
    components: {
        // eslint-disable-next-line vue/no-unused-components
        SpecialTitle
    },
    methods: {
        convertRouter() {
            this.$emit('convertRouter')
        }
    }
}
</script>

